<script>
import dataJson from "./../assets/json";
export default {
  data() {
    return {
			keywords: "",
			showSearch: false,
			selectComponent: "",
			selectDesigns: "",
			selectSeveritys: "",
			selectCompKnow: "",
			activeIndex: '1'
    };
  },
  created() {
		this.components = this.selectFun(dataJson.rules,'component')
		this.designs = this.selectFun(dataJson.rules,'design')
		this.severitys = this.selectFun(dataJson.rules,'severity')
		this.compKnow = this.selectFun(dataJson.rules,'compKnow')
	},
	computed: {
		data() {
			var that = this,
					data = dataJson.rules,
					resultData = [];
			if(this.keywords.length > 0) {
				data.forEach((item, index) => {
					if(that.searchFunc(item.ruleID, that.keywords) || that.searchFunc(item.name, that.keywords) ||
						that.searchFunc(item.content, that.keywords)) {
							resultData.push(item);
					}
				})
			}else {
				resultData = data;
			}
			if(that.selectComponent || that.selectDesigns || that.selectSeveritys || that.selectCompKnow) {
				resultData = resultData.filter(item => {
					return (that.selectComponent ? item.component == that.selectComponent : true) &&
					(that.selectDesigns ? item.design == that.selectDesigns : true) &&
					(that.selectSeveritys ? item.severity == that.selectSeveritys : true) &&
					(that.selectCompKnow ? item.compKnow == that.selectCompKnow : true) 
				})
			}
			return resultData
		},
		resultNum() {
			return this.data.length
		}
	},
	methods: {
		selectFun (arr,type) {
			let data = []
			arr.forEach(item=>{
				if(!data.includes(item[type+''])){
					data.push(item[type+''])
				}
			})
			return data
		},
		searchFunc (data, keywords) {
			var matched = true,
					keywordsArr = [],
					data = data ? data.toString().trim().toLowerCase() : "",
					keywords = keywords ? keywords.toString().trim().toLowerCase() : "";
			if(keywords && data.indexOf(keywords) < 0) {
				keywordsArr = keywords.split(" ");
				keywordsArr.forEach((item, index) => {
					if(data.indexOf(item) < 0) {
						matched = false;
						return false;
					}
				})
			}
			return matched;
		},
		showSearchInput() {
			this.showSearch = true;
		},
		closeSearch() {
			this.showSearch = false;
		},
		handleSelect(key, event) {
			this.activeIndex = key;
		}
	}
};
</script>

